import { uploadFile } from "../base.api.js";

export const uploadFileApi = async (file, companyName, mdsNumber, manualType) => {
    const response = await uploadFile('files/upload', file, {
        companyName: companyName,
        mdsNumber: mdsNumber,
        manualType: manualType
    });
    console.log('Upload response:', response);
    return response;
}