// src/pages/DashboardPage.jsx
import React, { useEffect, useState } from 'react';
import CreatableSelect from 'react-select/creatable';
import Button from '../../components/ui/Button.js';
import Card from '../../components/ui/Card.js';
import Input from '../../components/ui/Input.js';
import { uploadFileApi } from '../../api/uploadFile/fileupload.api.js';
import { getAllCompany } from '../../api/company/companyh.api.js';
import './DashboardPage.css';

const DashboardPage = ({ user, onLogout }) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [isUploading, setIsUploading] = useState(false);
  const [companyOptions, setCompanyOptions] = useState([]);
  const [formData, setFormData] = useState({
    companyName: '',
    mdsNumber: '',
    manualType: ''
  });
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchCompanies = async () => {
      try {
        const res = await getAllCompany();
        const data = res.data?.data || [];
        const opts = data.map(c => ({ value: c.companyName, label: c.companyName }));
        setCompanyOptions(opts);
      } catch {
        setError('Failed to fetch companies. Please try again later.');
      }
    };
    fetchCompanies();
  }, []);

  const handleSelectChange = option => {
    setFormData(prev => ({ ...prev, companyName: option ? option.value : '' }));
    setError('');
  };

  const handleCreateOption = inputValue => {
    const newOption = { value: inputValue, label: inputValue };
    setCompanyOptions(prev => [...prev, newOption]);
    setFormData(prev => ({ ...prev, companyName: inputValue }));
  };

  const handleInputChange = field => e => {
    setFormData(prev => ({ ...prev, [field]: e.target.value }));
    setError('');
  };

  const handleFileChange = e => {
    const file = e.target.files[0];
    if (!file) return;
    if (file.type !== 'application/pdf') {
      setError('Please select a PDF file');
      return;
    }
    if (file.size > 10 * 1024 * 1024) {
      setError('File size must be less than 10MB');
      return;
    }
    setSelectedFile(file);
    setError('');
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!selectedFile) {
      setError('Please select a PDF file to upload');
      return;
    }
    const { companyName, mdsNumber, manualType } = formData;
    if (!companyName || !mdsNumber || !manualType) {
      setError('Please fill in all fields');
      return;
    }

    setIsUploading(true);
    setUploadProgress(0);
    setError('');

    try {
      const uploadInterval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 90) { clearInterval(uploadInterval); return 90; }
          return prev + 10;
        });
      }, 200);

      const response = await uploadFileApi(
        selectedFile,
        companyName,
        mdsNumber,
        manualType
      );
      clearInterval(uploadInterval);
      setUploadProgress(100);

      if (response.error) {
        setError(`Upload failed: ${response.data?.error || 'Unknown error'}`);
        setIsUploading(false);
        setUploadProgress(0);
      } else {
        setTimeout(() => {
          alert('Manual uploaded successfully!');
          setSelectedFile(null);
          setUploadProgress(0);
          setIsUploading(false);
          setFormData({ companyName: '', mdsNumber: '', manualType: '' });
        }, 500);
      }
    } catch {
      setError('Upload failed. Please try again.');
      setIsUploading(false);
      setUploadProgress(0);
    }
  };

  const manualTypes = [
    { value: '', label: 'Select Manual Type' },
    { value: 'spare_manual', label: 'Spare manual' },
    { value: 'operation_manual', label: 'Operation manual' },
    { value: 'other_manual', label: 'Other' },
  ];

  return (
    <div className="dashboard-page">
      <div className="dashboard-page__container">
        <header className="dashboard-header">
          <div className="dashboard-header__content">
            <div className="dashboard-header__user">
              <div className="dashboard-header__avatar">
                {user?.name?.charAt(0).toUpperCase() || 'U'}
              </div>
              <div className="dashboard-header__info">
                <h1 className="dashboard-header__title">
                  Welcome back, {user?.name || 'User'}!
                </h1>
                <p className="dashboard-header__subtitle">{user?.email}</p>
              </div>
            </div>
            <Button
              variant="outline"
              onClick={onLogout}
              className="dashboard-header__logout"
            >
              Sign Out
            </Button>
          </div>
        </header>

        <main className="dashboard-content">
          <div className="dashboard-grid">
            <Card
              title="Upload User Manual"
              subtitle="Upload your company's user manual in PDF format"
              variant="elevated"
              className="dashboard-card dashboard-card--full-width"
            >
              <form onSubmit={handleSubmit} className="upload-form">
                {error && (
                  <div className="upload-form__error-banner">{error}</div>
                )}

                <div className="upload-form__field">
                  <label htmlFor="companyName">Company Name</label>
                  <CreatableSelect
                    id="companyName"
                    isClearable
                    options={companyOptions}
                    value={
                      formData.companyName
                        ? {
                            value: formData.companyName,
                            label: formData.companyName
                          }
                        : null
                    }
                    onChange={handleSelectChange}
                    onCreateOption={handleCreateOption}
                    placeholder="Type or select company"
                    classNamePrefix="react-select"
                  />
                </div>

                <div className="upload-form__field">
                  <Input
                    label="MDS Number"
                    name="mdsNumber"
                    placeholder="Enter your MDS number"
                    value={formData.mdsNumber}
                    onChange={handleInputChange('mdsNumber')}
                    required
                  />
                </div>

                <div className="upload-form__field">
                  <label className="upload-form__label">
                    Manual Type
                    <span className="upload-form__required">*</span>
                  </label>
                  <select
                    name="manualType"
                    value={formData.manualType}
                    onChange={handleInputChange('manualType')}
                    className="upload-form__select"
                    required
                  >
                    {manualTypes.map(type => (
                      <option key={type.value} value={type.value}>
                        {type.label}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="upload-form__field">
                  <label className="upload-form__label">
                    PDF File
                    <span className="upload-form__required">*</span>
                  </label>
                  <div
                    className="upload-form__file-input"
                    onClick={() => document.getElementById('file-input').click()}
                  >
                    <input
                      id="file-input"
                      type="file"
                      accept=".pdf"
                      onChange={handleFileChange}
                      className="upload-form__file"
                      disabled={isUploading}
                    />
                    <div className="upload-form__file-info">
                      {selectedFile ? (
                        <div className="upload-form__file-details">
                          <span className="upload-form__file-name">
                            {selectedFile.name}
                          </span>
                          <span className="upload-form__file-size">
                            ({(selectedFile.size / 1024 / 1024).toFixed(2)} MB)
                          </span>
                        </div>
                      ) : (
                        <span className="upload-form__file-placeholder">
                          Click to choose PDF file (max 10MB)
                        </span>
                      )}
                    </div>
                  </div>
                </div>

                {isUploading && (
                  <div className="upload-form__progress">
                    <div className="upload-form__progress-bar">
                      <div
                        className="upload-form__progress-fill"
                        style={{ width: `${uploadProgress}%` }}
                      />
                    </div>
                    <span className="upload-form__progress-text">
                      Uploading... {uploadProgress}%
                    </span>
                  </div>
                )}

                <div className="upload-form__actions">
                  <Button
                    type="submit"
                    variant="primary"
                    size="large"
                    loading={isUploading}
                    disabled={!selectedFile}
                    className="upload-form__submit"
                  >
                    {isUploading ? 'Uploading...' : 'Upload Manual'}
                  </Button>
                </div>

                <div className="upload-form__footer">
                  <p className="upload-form__info">
                    <strong>Supported formats:</strong> PDF only
                    <br />
                    <strong>Maximum file size:</strong> 10MB
                    <br />
                    <strong>Manual types:</strong> Spare manual, Operation manual,
                    Other
                  </p>
                </div>
              </form>
            </Card>
          </div>
        </main>
      </div>
    </div>
  );
};

export default DashboardPage;
